@font-face {
  font-family: "Montserrat";
  src: url("..//fonts/Montserrat-Bold.woff2")
    format("woff2"),
    url("..//fonts/Montserrat-Bold.woff") format ("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Raleway";
  src: url("..//fonts/Raleway-ExtraBold.woff2") format("woff2"),
    url("..//fonts/Raleway-ExtraBold.woff") format ("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}


